import { mapState,mapMutations} from 'vuex'
export default{
	data(){
		return{
			relationType:''
		}
	},
	computed:{
		...mapState({
			msgType:state=>state.msgType
		})
	},
	methods:{
		...mapMutations([
			'getMessage'
		]),
		getRelateType(type){
			this.relationType=type
			this.$emit('sendRelation',{ relationType:type})
		}	
	}
}
